
.App {
  min-width: 320px;
  max-width: 1280px;
  min-height: 100vh;
  font-family: Inter, Arial, sans-serif;
  margin: auto;
  display: grid;
  text-rendering: optimizeLegibility;
  grid-template-rows: min-content;
  
} 

.link{
  text-decoration: none;
  color: black;
}

.link_selected{
  font-weight: 500;
}

.link:hover{
  opacity: 70%;
  cursor: pointer;
}

.text{
  margin: 0;
}

.error{
  font-size: 2rem;
  color: red;
  font-weight: 500;
  text-align: center;
  
}